.app__footer {
    flex: 1;
    width: 100%;
    flex-direction: column;
    margin: 3rem;
    // font-family: Trench Slab;
}

.app__footer-cards {
    width: 60%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap-reverse;
    margin: 1rem 2rem 4rem;

    .app__footer-card {
        min-width: 290px;
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;

        margin: 1rem 0;
        padding: 1rem;
        border-radius: 10px;
        cursor: pointer;
        background-color: oldlace;   

        transition: all 0.3s ease-in-out;

        img {
            width: 40px;
            height: 40px;
            margin: 0 0.7rem;
        }

        p {
            font-weight: 600;
        }

        a {
            text-decoration: none;
            font-weight: 600;
            color: black;
        }

        &:hover {
            box-shadow: 0 0 20px #333;
        }

        @media screen and (max-width: 450px) {
            width: 100%;
        }
    }

    @media screen and (max-width: 760px) {
        width: 100%;
    }

}

// .app__footer-cards ,.app__footer-card:last-child {
//     background-color: #f2f7fb
// }

.app__footer-form {
    width: 60%;
    flex-direction: column;
    margin: 1rem 2rem;

    div {
        width: 100%;

        margin: 0.75rem 0;
        border-radius: 10px;
        cursor: pointer;
        background-color: transparent;

        transition: all 0.3s ease-in-out;

        input, textarea {
            width: 100%;
            padding: 0.95rem;
            border: none;
            border-radius: 7px;
            background-color: oldlace;

            font-family: var(--font-base);
            color: var(-secondary-color);
            outline: none;
        }

        textarea {
            height: 180px;
        }

        // &:hover {
        //     box-shadow: 0 0 25px var(--primary-color);
        // }
    }

    button {
        padding: 1rem 2rem;
        border-radius: 10px;
        border: none;
        background-color: #333;
        background-image: linear-gradient(to top, #1e3c72 0%, #1e3c72 1%, #2a5298 100%);

        font-weight: 500;
        color: white;
        outline: none;
        margin: 2rem 0 0 0;
        // font-family: var(--font-base);    
        // font-family: Trench Slab;
        cursor: pointer;
        transition: all 0.3s ease-in-out;
        
        &:hover {
            box-shadow: 0 0 25px var(--gray-color);
        }
    }

    @media screen and (max-width: 768px) {
        width: 100%;
    }

    .contact-text {
        font-weight: 700;
    }
}

